import React from 'react';
import Gauge from 'react-svg-gauge';
class UDGauge extends React.Component {

  // state is for keeping control state before or after changes.
  state = {

    // the things you want to put in state.
    // text: this.props.text //un comment the line to use state insted props
  }


  render() {

      // These props are returned from PowerShell!
      // return <h1>{this.state.text}</h1> // un comment the line to render using value from state.

      return ( <div>
        <Gauge value={this.props.value} width={this.props.width} height={this.props.height} label={this.props.label} color={this.props.color} backgroundColor={this.props.backgroundColor} minMaxLabelStyle={this.props.minMaxLabelStyle} topLabelStyle={this.props.topLabelStyle} valueLabelStyle={this.props.valueLabelStyle} />
        </div>
      );
  }
}

export default UDGauge
